import React, { Suspense, lazy } from 'react';
import { BrowserRouter as Router, Routes, Route  } from 'react-router-dom';
import './App.css';
import { HomePage } from './VAR';
import { Loading } from './components/loading';
import { NotificationContainer } from 'react-notifications';


import _ from './DB';

const Layout = lazy(() => import('./pages/layout/layout'));

const Login = lazy(() => import('./pages/login/login'));
const Register = lazy(() => import('./pages/register/register'));
const Manage = lazy(() => import('./pages/manage/manage'));
const Create = lazy(() => import('./pages/create/create'));
const Edit = lazy(() => import('./pages/edit/edit'));
const JoinList = lazy(() => import('./pages/join/join-list'));
const Join = lazy(() => import('./pages/join/join'));
const ManageJoin = lazy(() => import('./pages/join/manage-join'));
const NoMatch = lazy(() => import('./pages/no-match/no-match'));



const App = () => (
  <div>
    <Router basename={HomePage}>
      <Suspense fallback={<Loading />}>
          <Routes>
            <Route path="/login" element={<Login />} />
            <Route path="/register" element={<Register />} />
            <Route element={<Layout />}>
              <Route path="/manage" element={<Manage />} />
              <Route path="/create" element={<Create />} />
              <Route path="/edit/:id" element={<Edit />} />
              <Route path="/join" element={<JoinList />} />
              <Route path="/join/:id" element={<Join />} />
              <Route path="/manage/join/:id" element={<ManageJoin />} />
            </Route>
            <Route path="*" element={<NoMatch />} />
          </Routes>
      </Suspense>
  </Router>
  <NotificationContainer />
  </div>
);



export default App;
